@charset "utf-8";
/* Reset */
* {
  outline: 0 !important;
}
@import url("https://fonts.googleapis.com/css2?family=Croissant+One&family=Pacifico&display=swap");
/* font-family: 'Croissant One', cursive;
font-family: 'Pacifico', cursive; */
@font-face {
  font-family: "IBMPlexSansKR-Regular";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-07@1.0/IBMPlexSansKR-Regular.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}
html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
div,
p,
blockquote,
pre,
code,
address,
ul,
ol,
li,
menu,
nav,
section,
article,
aside,
dl,
dt,
dd,
table,
thead,
tbody,
tfoot,
label,
caption,
th,
td,
form,
fieldset,
legend,
hr,
input,
button,
textarea,
object,
figure,
figcaption {
  margin: 0;
  padding: 0;
}
html,
body {
  width: 100%;
}
html {
  /* font-size: 20px; */
}
body {
  width: 100%;
  min-width: 320px;
  -webkit-text-size-adjust: none;
  word-wrap: break-word;
  word-break: break-all;
  line-height: 1.2;
  font-family: "IBMPlexSansKR-Regular";
  /* font-size: 20px; */
}
body,
input,
select,
textarea,
button {
  border: none;
  font-family: "IBMPlexSansKR-Regular";
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: inherit;
}
ul,
ol,
li {
  list-style: none;
}
table {
  width: 100%;
  border-spacing: 0;
  border-collapse: collapse;
}
img,
fieldset {
  border: none;
}
address,
cite,
code,
em {
  font-style: normal;
  font-weight: normal;
}
label,
img,
input,
select,
textarea,
button {
  vertical-align: middle;
}
.hide,
caption,
legend {
  line-height: 0;
  overflow: hidden;
}
hr {
  display: none;
}
main,
header,
section,
nav,
footer,
aside,
article,
figure {
  display: block;
}
a {
  color: #262626;
  text-decoration: none;
  cursor: pointer;
}
/* Form */
textarea {
  border: 1px solid #dbdbdb;
}
select {
  height: 32px;
  /* font-size: 0.7rem; */
  color: #373737;
  border: 1px solid #e9e9e9;
  background: #fff;
  border-radius: 5px;
}
input[type="tel"],
input[type="time"],
input[type="text"],
input[type="password"],
input[type="search"],
input[type="email"],
input[type="file"],
input[type="url"],
input[type="number"],
input[type="date"],
textarea {
  width: 100%;
  height: 34px;
  color: #484848;
  border: none;
  background: #f2f2f2;
  text-indent: 17px;
  border-radius: 20px;
  vertical-align: middle;
}
input::-webkit-input-placeholder {
  color: #979797;
  line-height: 1;
}
textarea {
  padding: 5px 0;
}
select:focus,
textarea:focus,
input:focus {
  border: none;
}
input[type="tel"][readonly],
input[type="text"][readonly],
input[type="password"][readonly],
input[type="email"][readonly],
input[type="search"][readonly],
input[type="tel"][disabled],
input[type="text"][disabled],
input[type="password"][disabled],
input[type="search"][disabled],
input[type="email"][disabled] {
  background: #eaeaea;
  border-color: #c0c0c0;
  color: #666;
}
textarea[readonly],
textarea[disabled] {
  padding: 11px;
  color: #666;
  font-weight: normal;
  height: 78px;
  background: #eaeaea;
  border: 1px solid #c0c0c0;
}
/* 변수선언 */
:root {
  /* size */
  /* body 기본 16px 기준 */
  --homeNamerem: 6rem; /* 96px */
  --h1rem: 4rem; /* 64px */
  --h2rem: 3rem; /* 48px */
  --h3rem: 2.25rem; /* 36px */
  --h4rem: 1.88rem; /* 30px */
  --h5rem: 1.5rem; /* 24px */
  --h6rem: 1.25rem; /* 20px */
  --labelrem: 1.13rem; /* 18px */
  --baseSizerem: 1rem; /* 16px */
  --smallrem: 0.75rem; /* 12px */
  --minrem: 0.63rem; /* 10px */

  --homeName: 5vw; /* 96px */
  --h1: 3.33vw; /* 64px */
  --h2: 2.5vw; /* 48px */
  --h3: 1.88vw; /* 36px */
  --h4: 1.56vw; /* 30px */
  --h5: 1.25vw; /* 24px */
  --h6: 1.04vw; /* 20px */
  --label: 0.94vw; /* 18px */
  --baseSize: 0.83vw; /* 16px */
  --small: 0.36vw; /* 12px */
  --min: 0.52vw; /* 10px */
  /* color */
  --primary: #363062;
  --secondary: #4d4c7d;
  --point: #fd841f;
  --subPoint: #e4d192;
  --base: #fffbf5;
  --nameKr: #512b81;
  --nameEs: #944dcc;
  --slide: #827397;
}
